    import { getAddressList, setDefaultAddress, delAddress, setUseAddress } from '@/api/address.js'
    import SlipDel from 'vue-slip-delete'
    import noData from '@/components/noData.vue'
    export default {
      components: {
        SlipDel,
        noData
      },
      data () {
        return {
          showstatus: false,
          addresstop: true,
          showtoast: false,
          showstatus: 0,
          toast: '',
          nowaddressid: '',
          addresslist: []
        }
    },
    methods: {
      setdefaulta: function (addressid) {
        this.toast = '将此地址设为默认地址？'
        this.showtoast = true
        this.showstatus = 0
        this.nowaddressid = addressid
      },
        // 使用该地址
      setUseAddress: function (addressid) {
        var from = window.localStorage.getItem('isFromOrder')
        if (from != 0) {
          const postData = {
            id: addressid
          }
          setUseAddress(postData)
                    .then(res => {
                      if (res.data.errorcode === 0) {
                        this.$router.push({path: '/confirmOrder', query: { id: addressid, preid: from}})
                      }
                    })
                    .catch(err => {
                      this.$vux.toast.text('网络异常！', 'middle')
                    })
        }
      },
      defaulsure () {
        this.showtoast = false
        if (this.showstatus == 0) {
          const postData = {
            id: this.nowaddressid
          }
          setDefaultAddress(postData)
                .then(res => {
                  if (res.data.errorcode === 0) {
                    this.$vux.toast.text('设置成功', 'middle')
                    this.showstatus = false
                    this.AddressList()
                  } else {
                    this.$vux.toast.text(res.data.errormsg, 'middle')
                  }
                })
                .catch(err => {
                  this.$vux.toast.text('网络异常！', 'middle')
                })
        } else {
          this.deladdressRequest()
        }
      },
    slipOpen (vm) {
    },
    del (listid) {
      this.toast = '确认删除此地址？'
    this.showtoast = true
    this.showstatus = 1
    this.nowaddressid = listid
},
    deladdressRequest (listid) {
      const postData = {
        id: this.nowaddressid
      }
    const that = this
    delAddress(postData)
        .then(res => {
          if (res.data.errorcode === 0) {
            this.$vux.toast.text('删除地址成功', 'middle')
            this.showstatus = false
            this.addresslist = []
            this.AddressList()
          } else {
            this.$vux.toast.text(res.data.errormsg, 'middle')
          }
        })
        .catch(err => {
          this.$vux.toast.text('网络异常！', 'middle')
        })
    },
    noshow () {
      this.showtoast = false
},
    AddressList () {
      const postData = {}
      const that = this
      getAddressList(postData)
            .then(res => {
              if (res.data.errorcode === 0) {
                that.addresslist = res.data.data.addresslist
                if (res.data.data.addresslist.length == 0) {
                  this.showstatus = true
                  this.addresstop = false
                };
              } else {
                this.$vux.toast.text(res.data.errormsg, 'middle')
              }
            })
            .catch(err => {
              this.$vux.toast.text('网络异常！', 'middle')
            })
    }
    },
    created () {
      this.AddressList()
    }
    }



